
// done by riverco.de
$header-menu-item-color: #ac975e;
$contact-phone: #aac8ea;
a {
	text-decoration: none; }
header {
	background: #375272;
	.container {
		padding-top: 25px;
		padding-bottom: 25px;
		position: relative; } }

.header-logo {
	margin: 15px 40px 0 18px;
	display: inline-block; }
.header-menu {
	display: inline-block;
	max-width: 570px;
	width: 100%;
	padding-left: 150px;
	margin-top: 20px;
	vertical-align: top;
	position: relative; }
.header-menu__item {
	display: inline-block;
	width: 125px;
	margin-right: 15px;
	&:first-of-type {
		position: absolute;
		left: 0px;
		margin-right: 0;
		top: auto;
		width: 80px;
		&:before {
			content: '';
			position: absolute;
			right: -37px;
			height: 38px;
			top: 50%;
			margin-top: -19px;
			width: 1px;
			background: #fff; } }
	&:nth-of-type(3n+1) {
		margin-right: 0; }
	a {
		color: $header-menu-item-color;
		text-transform: uppercase;
		transition: all .3s;
		font: 400 14px/18px "Open Sans";
		&:hover {
			color: #fff;
			text-decoration: underline; } } }
.contact-phone {
	color: $contact-phone;
	font: 700 24px "Open Sans";
	float: right;
	margin-right: 20px;
	text-transform: uppercase;
	vertical-align: top; }
.callback {
	color: #fff;
	font: 400 14px "Open Sans";
	text-transform: uppercase; }
.header-callback {
	position: absolute;
	text-align: right;
	right: 30px;
	top: 72px;
	width: 80px; }
.header-callback__img {
	position: absolute;
	width: 102px;
	height: 102px;
	right: 112px;
	top: 78px;
	background: #8a2222;
	border: 1px solid #fff;
	border-radius: 50%;
	img {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%); } }



